*{
    box-sizing: border-box;
}
.AboutSection{
    text-align: center;
  
}
#PRJSS{
  display: flex;
  flex-direction:row;
  justify-content: center;
  align-items: center;
   animation-name: prjs;
   animation-duration: 2000ms ;
}
@keyframes prjs{
    0%{
        transform: translateX(-100%);
    }
   
    100%{
        transform: translateX(0%);
    }
}

#prj2{
     display: flex;
     align-items: center;
     transition: all 0.2s;
}
#prj2:hover{
   color:rgb(55, 82, 55);  

}
.project1{
 position: relative;
display: flex;
flex-direction: column;
justify-content: flex-end;
overflow: hidden;
width: 25rem;
 height:500px;
 
}

#pr2{
    animation: prjs2;
    animation-duration: 1000ms;  
    animation-fill-mode: forwards;
}
@keyframes prjs2{
    0%{
        transform:rotateX(90deg);

    }
    90%{
        transform:rotateY(180deg);

    }
    100%{
        transform:rotateX(0deg);
    }
}


.project-Image{
   position: absolute;  
    width: fit-content;
    z-index: -1;
    transition: all 0.2s linear;
}
.project-Name{
    display: flex;
    padding-top: 10px;
    padding:70px;
    background-color: rgba(0, 0, 0, 0.5);
    flex-direction: column;
     opacity: 0;
      color: aliceblue;
     transition: all 1.5s;
}

.project-Name:hover {
    opacity: 1;
}
.project1:hover{
   
    transform: scaleY(0.9);
    transition: all 0.5s linear;
}


#progress{
    width:100%;
    padding: 10px;
     margin-left: 20px;
     border-radius: 1000px;
     animation: progres1;
     animation-duration: 3000ms;
     animation-delay: 200ms;

}
@keyframes progres1{
    0%{
        transform: translateX(-100%);
        transition: all 0.5s;
    }
    
    
    100%{
        transform: translateX(0%);
        transition: all 0.5s;
    }
}
.progressbar{
    height:30px;
    background:rgb(132, 147, 162);
    border-radius:1000px;
    font-size: large;
     color: blanchedalmond;
     transition: all 0.5s;

}
 .progressbar:hover{
     transform: scaleX(1.1);
     font-size: small;

 }
.head3{
    background: #121fcf78;
background: linear-gradient(to right, #8287cd 0%, #26798b 100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}
.head3:hover{
    transform: scaleX(1.1);
   transition: all 0.5s;
}
