*{
    box-sizing: border-box;
}
.intro{
display: flex;
justify-content: center;  
overflow: hidden;
}
.listtypes{
    color: aliceblue;
    transition: all 1s;
}
.listtypes:hover{
    color: aqua;
    transform: scaleX(1.1);
}
 .intr1{
     position: relative;
     display: flex;
     flex-direction:column;
     align-items: center;
     justify-content: space-between;
     height:600px;
     overflow: hidden;
    
 }


.intro > video{
     position: absolute;
     z-index: -1;
     left: 0;
     right: 0;
      width: 100%;
     object-fit: cover;
     
}

.text-warpper{
    width: 100%;
    display: flex;
    font-weight: bold;
    background-size: cover;
    color: aliceblue;
    mix-blend-mode:difference;
    justify-content: center;
    animation-name: text-loader;
    animation-duration: 2000ms;
}
.text-warpper >span{
     font-size:20px;
     text-transform: capitalize;
     color:whitesmoke;
      font-weight: bold;
}

@keyframes text-loader{
     0%{
         transform:translateX(-100%)
     }
     100%{
         transform: translateX(0%);
     }
}
   

 

 